@import url("https://fonts.googleapis.com/css2?family=Lato:wght@700;900&family=Poppins:wght@100;200;300;400;500&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  font-family: "Poppins", sans-serif;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 1rem;
  overflow: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.flex {
  display: flex;
  align-items: center;
}
.flex__col {
  flex-direction: column;
}
.flex__c {
  justify-content: center;
}
.flex__sb {
  justify-content: space-between;
}
.flex__sa {
  justify-content: space-around;
}
.flex__se {
  justify-content: space-evenly;
}

.px-1 {
  padding: 0 1rem;
}

.px-2 {
  padding: 0 1.5rem;
}

.px-3 {
  padding: 0 2rem;
}

.px-4 {
  padding: 0 3rem;
}

.mx-1 {
  margin: 0 1rem;
}

.mx-2 {
  margin: 0 1.5rem;
}

.mx-3 {
  margin: 0 2rem;
}

.mx-4 {
  margin: 0 3rem;
}

.py-1 {
  padding: 1rem 0;
}

.py-2 {
  padding: 1.5rem 0;
}

.py-3 {
  padding: 2rem 0;
}

.py-4 {
  padding: 3rem 0;
}

.my-1 {
  margin: 1rem 0;
}

.my-2 {
  margin: 1.5rem 0;
}

.my-3 {
  margin: 2rem 0;
}

.my-4 {
  margin: 3rem 0;
}

#main__nav .logo__container {
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #333;
}
#main__nav .logo__container .logo {
  display: inline-block;
  height: 30px;
  width: 30px;
  margin-right: 0.25rem;
}
#main__nav .logo__container .logo__title {
  font-weight: 300;
  margin-top: 0.75rem;
}
#main__nav .list li {
  list-style: none;
}
#main__nav .list li a {
  padding: 0 1rem;
  color: #333;
  transition: color 300ms ease-in-out;
}
#main__nav .list li a:hover {
  color: #0057FF;
}

.btn__bl {
  display: inline-block;
  padding: 0.75rem 2rem;
  margin: 1rem;
  border: none;
  border-radius: 3.125rem;
  transition: box-shadow 250ms ease-in-out;
  cursor: pointer;
}

.btn__bl {
  background-color: #0057FF;
  color: #fff;
  box-shadow: 0 0.1875rem 0.75rem rgba(0, 87, 255, 0.5);
}
.btn__bl:hover {
  box-shadow: none;
}